.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 20px;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 20px;
}

.dialogsItems a {
    color: black;
    text-decoration: none;
}

.dialogsItems a:hover {
    color: chocolate;
}